import React, { useEffect, useState } from "react";
import { Field, Formik, Form, ErrorMessage } from "formik";
import { useDispatch, useSelector } from "react-redux";
import { login, signupAction } from "../../actions/userAction";
import { useHistory } from "react-router-dom";
import { Modal } from "react-bootstrap";
import * as yup from "yup";
export default function Login() {
  const dispatch = useDispatch();
  const history = useHistory();
  const { user, error, errorSignin, userSignin } = useSelector(
    (state) => state.userReducer
  );
  const checkAccount = () => {
    if (user && error === false) {
      history.goBack();
    }
  };

  const loginSchema = yup.object().shape({
    taiKhoan: yup
      .string()
      .required("Ti khon khng c b trng"),
    matKhau: yup
      .string()
      .required("Mt khu khng c b trng")
      .min(6, "Mt khu cn di hn 6 k t"),
  });
  const signupValidation = yup.object().shape({
    hoTen: yup
      .string()
      .matches(
        /^[a-zA-Z]+(([',. -][a-zA-Z ])?[a-zA-Z]*)*$/,
        "Tn khng bao gm s"
      )
      .min(2, "Qu ngn!")
      .max(70, "Qu Di!")
      .required("H tn khng c b trng!"),
    soDt: yup
      .number()
      .required("S in thoi khng c b trng!")
      .typeError("S in thoi ch c cha s!"),
    email: yup
      .string()
      .required("Email khng c b trng!")
      .email("Email khng hp l!"),
    matKhau: yup
      .string()
      .required("Mt khu khng c b trng!")
      .min(6, "Mt khu qu ngn!"),
    nhapLaiMatKhau: yup
      .string()
      .required("Mt khu khng c b trng!")
      .min(6, "Mt khu qu ngn!"),
    maLoaiNguoiDung: yup
      .string()
      .matches(
        /^[a-zA-Z]+(([',. -][a-zA-Z ])?[a-zA-Z]*)*$/,
        "Loi ngi dng khng bao gm s"
      )
      .min(1, "Qu ngn!")
      .max(70, "Qu Di!")
      .required("M loi ngi dng khng c b trng!"),
    taiKhoan: yup
      .string()
      .min(1, "Qu ngn!")
      .max(70, "Qu Di!")
      .required("Ti khon khng c b trng!"),
    maNhom: yup
      .string()
      .min(1, "Qu ngn!")
      .max(70, "Qu Di!")
      .required("M nhm khng c b trng!"),
  });
  useEffect(() => {
    checkAccount();
  }, [error, user]);
  const [signupShow, setSignupShow] = useState(false);
  const [checkPassword, setCheckPassword] = useState(true);
  const [signinDone, setSigninDone] = useState(true);

  const renderErrorOrNot = () => {

    if (signinDone === true) {
      if (errorSignin === false && userSignin !== null) {
        return (
          <>
            <div
              className="text-success text-center"
              style={{ height: "10px", margin: " 5px 0" }}
            >
              <p>ng k thnh cng</p>
            </div>
          </>
        );
      } else {
        return (
          <div
            className="text-danger text-center"
            style={{ height: "10px", margin: " 5px 0" }}
          ></div>
        );
      }
    } else if (errorSignin === true) {
      return (
        <>
          <div
            className="text-danger text-center"
            style={{ height: "10px", margin: " 5px 0" }}
          >
            <p> xy ra li</p>
          </div>
        </>
      );
    }
  };
  const handleClose = () => {
    setSignupShow(false);
    setSigninDone(false);
  };
  return (
    <div className="login">
      <div className="login__layout"></div>

      <div className="login__form">
        <div className="wrap_form">
          <a
            onClick={() => {
              history.push("/");
            }}
            className="close-login"
          >
            <i className="fa fa-times"></i>
          </a>

          <img
            src="/img/layer1.png"
            alt="LogoBrand"
            style={{ maxWidth: "70px", maxHeight: "70px" }}
          />
          <p style={{ marginBottom: "70px" }}>
            Th gii phim trn u ngn tay
          </p>
          <p style={{ marginBottom: "10px" }}>
            ng nhp  c nhiu u i, mua v v bo mt thng tin!
          </p>
          <Formik
            initialValues={{
              taiKhoan: "",
              matKhau: "",
            }}
            onSubmit={(values) => {
              dispatch(login(values));
            }}
            validationSchema={loginSchema}
            render={(formikProps) => (
              <Form>
                <div className="form-group m-0">
                  <Field
                    type="text"
                    name="taiKhoan"
                    className="form-control"
                    placeholder="Ti khon"
                    onChange={formikProps.handleChange}
                  />
                  <div
                    className="text-danger"
                    style={{ height: "20px", margin: " 5px 0" }}
                  >
                    <ErrorMessage name="taiKhoan" />
                  </div>
                </div>
                <div className="form-group" style={{ marginBottom: "10px" }}>
                  <Field
                    type="password"
                    name="matKhau"
                    className="form-control"
                    placeholder="Mt khu"
                    onChange={formikProps.handleChange}
                  />
                  <div
                    className="text-danger"
                    style={{ height: "20px", margin: " 5px 0" }}
                  >
                    <ErrorMessage name="matKhau" />
                    {error ? (
                      <p style={{ height: "20px", color: "red" }}>
                        Ti khon hoc mt khu khng ng. Vui lng th li!
                      </p>
                    ) : (
                      <p style={{ height: "20px" }}></p>
                    )}
                  </div>
                </div>

                <button type="submit" className="btn btn-login mt-4">
                  ng nhp
                </button>
              </Form>
            )}
          />
          <div style={{ marginTop: "10px", fontSize: "16px" }}>
            <a
              onClick={() => {
                setSignupShow(true);
              }}
            >
              ng k
            </a>
          </div>
          <Modal
            show={signupShow}
            id="modal-changePassword"
            aria-labelledby="contained-modal-title-vcenter"
            centered
            onHide={handleClose}
          >
            <Modal.Body>
              <Formik
                initialValues={{
                  taiKhoan: "",
                  matKhau: "",
                  email: "",
                  soDt: "",
                  maNhom: "GP02",
                  maLoaiNguoiDung: "KhachHang",
                  hoTen: "",
                  nhapLaiMatKhau: "",
                }}
                onSubmit={(values) => {
                  if (values.nhapLaiMatKhau === values.matKhau) {
                    dispatch(
                      signupAction({
                        taiKhoan: values.taiKhoan,
                        matKhau: values.matKhau,
                        email: values.email,
                        soDt: values.soDt,
                        maNhom: "GP02",
                        maLoaiNguoiDung: "KhachHang",
                        hoTen: values.hoTen,
                      })
                    );
                    setTimeout(() => {
                      if (errorSignin !== false) {
                        setSigninDone(true);
                      } else {
                        setSigninDone(false);
                      }
                    }, 1000);
                  } else {
                    setCheckPassword(false);
                  }
                }}
                validationSchema={signupValidation}
                render={(formikProps) => (
                  <Form>
                    <div className="row">
                      <div className="col-12 col-md-6">
                        <div className="form-group m-0">
                          <Field
                            type="text"
                            name="taiKhoan"
                            className="form-control"
                            placeholder="Ti khon"
                            onChange={formikProps.handleChange}
                            
                          />

                          <div
                            className="text-danger text-center"
                            style={{ height: "20px", margin: " 5px 0" }}
                          >
                            <ErrorMessage name="taiKhoan" />
                          </div>
                        </div>
                        <div className="form-group m-0">
                          <Field
                            type="password"
                            name="matKhau"
                            className="form-control"
                            placeholder="Mt khu"
                            onChange={formikProps.handleChange}
                            
                          />

                          <div
                            className="text-danger text-center"
                            style={{ height: "20px", margin: " 5px 0" }}
                          >
                            <ErrorMessage name="matKhau" />
                          </div>
                        </div>
                        <div className="form-group m-0">
                          <Field
                            type="password"
                            name="nhapLaiMatKhau"
                            className="form-control"
                            placeholder="Nhp li mt khu"
                            onChange={formikProps.handleChange}
                            
                          />
                          {checkPassword ? (
                            <div
                              className="text-danger text-center"
                              style={{ height: "20px", margin: " 5px 0" }}
                            >
                              <ErrorMessage name="nhapLaiMatKhau" />
                            </div>
                          ) : (
                            <div
                              className="text-danger text-center"
                              style={{ height: "20px", margin: " 5px 0" }}
                            >
                              <p>Mt khu nhp li khng ng</p>
                            </div>
                          )}
                        </div>
                        <div className="form-group m-0">
                          <Field
                            type="text"
                            name="email"
                            className="form-control"
                            placeholder="Email"
                            onChange={formikProps.handleChange}
                            
                          />

                          <div
                            className="text-danger text-center"
                            style={{ height: "20px", margin: " 5px 0" }}
                          >
                            <ErrorMessage name="email" />
                          </div>
                        </div>
                      </div>
                      <div className="col-12 col-md-6">
                        <div className="form-group m-0">
                          <Field
                            type="text"
                            name="hoTen"
                            className="form-control"
                            placeholder="H tn"
                            onChange={formikProps.handleChange}
                            
                          />

                          <div
                            className="text-danger text-center"
                            style={{ height: "20px", margin: " 5px 0" }}
                          >
                            <ErrorMessage name="hoTen" />
                          </div>
                        </div>
                        <div className="form-group m-0">
                          <Field
                            type="text"
                            name="soDt"
                            className="form-control"
                            placeholder="S in thoi"
                            onChange={formikProps.handleChange}
                            
                          />

                          <div
                            className="text-danger text-center"
                            style={{ height: "20px", margin: " 5px 0" }}
                          >
                            <ErrorMessage name="soDt" />
                          </div>
                        </div>
                        {renderErrorOrNot()}
                        <div className="text-center">
                          <button
                            type="submit"
                            className="btn btn-secondary mt-2"
                          >
                            ng k
                          </button>
                        </div>
                      </div>
                    </div>
                  </Form>
                )}
              />
              <a
                onClick={() => {
                  handleClose();
                }}
              >
                <i className="fa fa-times"></i>
              </a>
            </Modal.Body>
          </Modal>
        </div>
      </div>
    </div>
  );
}
