import React, { useEffect, useState } from "react";
import { Tabs, Tab, Modal } from "react-bootstrap";
import { useSelector, useDispatch } from "react-redux";
import { Formik, ErrorMessage, Form, Field } from "formik";
import * as Yup from "yup";

import Loading from "../../components/loading";
import { getUserInfo, updateAction } from "../../actions/userAction";
export default function Customer() {
  const { user, userInfo } = useSelector((state) => state.userReducer);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getUserInfo({ taiKhoan: user?.taiKhoan }));
  }, [user]);
  
  const [isPassword, setIsPassword] = useState(true);
  const [isPasswordChange, setIsPasswordChange] = useState(true);
  const [checkNewPassword, setCheckNewPassword] = useState(true);
  const [changePasswordDone, setChangePasswordDone] = useState(false);
  const [modalShow, setModalShow] = useState(false);
  const [changePassword, setChangePassword] = useState(null);

  const handleClose = () => {
    setModalShow(false);
    setChangePassword(false);
    setChangePasswordDone(false);
  };

  const validation = Yup.object().shape({
    hoTen: Yup.string()
      .matches(
        /^[a-zA-Z]+(([',. -][a-zA-Z ])?[a-zA-Z]*)*$/,
        "Tn khng bao gm s"
      )
      .min(2, "Qu ngn!")
      .max(70, "Qu Di!")
      .required("H tn khng c b trng!"),
    soDT: Yup.number()
      .required("S in thoi khng c b trng!")
      .typeError("S in thoi ch c cha s!"),
    email: Yup.string()
      .required("Email khng c b trng!")
      .email("Email khng hp l!"),
    matKhau: Yup.string()
      .required("Mt khu khng c b trng!")
      .min(6, "Mt khu qu ngn!"),
  });

  const renderInfo = () => {
    return (
      <Formik
        initialValues={{
          hoTen: `${userInfo?.hoTen === null ? "" : userInfo?.hoTen}`,
          soDT: `${userInfo?.soDT === null ? "" : userInfo?.soDT}`,
          email: `${userInfo?.email === null ? "" : userInfo?.email}`,
          taiKhoan: `${userInfo?.taiKhoan === null ? "" : userInfo?.taiKhoan}`,
          matKhau: "",
        }}
        onSubmit={(values) => {
          if (values.matKhau !== userInfo.matKhau) {
            setIsPassword(false);
          } else {
            dispatch(
              updateAction({
                ...values,
                maNhom: user.maNhom,
                maLoaiNguoiDung: user.maLoaiNguoiDung,
              })
            );
            setModalShow(true);
          }
        }}
        validationSchema={validation}
      >
        {(props) => (
          <form onSubmit={props.handleSubmit}>
            <div className="row">
              <div className="col-12 col-md-6">
                <div>
                  <div className="input-group">
                    <div className="input-group-prepend">
                      <span className="input-group-text">H tn</span>
                    </div>
                    <input
                      type="text"
                      className="form-control"
                      onChange={props.handleChange}
                      onBlur={props.handleBlur}
                      value={props.values.hoTen}
                      name="hoTen"
                    />
                  </div>
                  <div
                    className="text-danger"
                    style={{
                      height: "20px",
                      fontSize: "14px",
                      textAlign: "center",
                    }}
                  >
                    <ErrorMessage name="hoTen" />
                  </div>
                </div>
                <div>
                  <div className="input-group">
                    <div className="input-group-prepend">
                      <span className="input-group-text">S in thoi</span>
                    </div>
                    <input
                      type="text"
                      className="form-control"
                      onChange={props.handleChange}
                      onBlur={props.handleBlur}
                      value={props.values.soDT}
                      name="soDT"
                    />
                  </div>
                  <div
                    className="text-danger"
                    style={{
                      height: "20px",
                      fontSize: "14px",
                      textAlign: "center",
                    }}
                  >
                    <ErrorMessage name="soDT" />
                  </div>
                </div>
                <div>
                  <div className="input-group">
                    <div className="input-group-prepend">
                      <span className="input-group-text">Email</span>
                    </div>
                    <input
                      type="text"
                      className="form-control"
                      onChange={props.handleChange}
                      onBlur={props.handleBlur}
                      value={props.values.email}
                      name="email"
                    />
                  </div>
                  <div
                    className="text-danger"
                    style={{
                      height: "20px",
                      fontSize: "14px",
                      textAlign: "center",
                    }}
                  >
                    <ErrorMessage name="email" />
                  </div>
                </div>
              </div>
              <div className="col-12 col-md-6">
                <div>
                  <div className="input-group">
                    <div className="input-group-prepend">
                      <span className="input-group-text">Ti khon</span>
                    </div>
                    <input
                      type="text"
                      className="form-control"
                      onChange={props.handleChange}
                      onBlur={props.handleBlur}
                      value={props.values.taiKhoan}
                      name="taiKhoan"
                      disabled
                    />
                  </div>
                  <div
                    className="text-danger"
                    style={{ height: "20px", fontSize: "14px" }}
                  >
                    <ErrorMessage name="taiKhoan" />
                  </div>
                </div>
                <div>
                  <div className="input-group">
                    <div className="input-group-prepend">
                      <span className="input-group-text">Mt khu</span>
                    </div>
                    <input
                      type="password"
                      className="form-control"
                      onChange={props.handleChange}
                      onBlur={props.handleBlur}
                      value={props.values.matKhau}
                      name="matKhau"
                      onClick={() => setIsPassword(true)}
                    />
                  </div>
                  {isPassword ? (
                    <div
                      className="text-danger"
                      style={{
                        height: "20px",
                        fontSize: "14px",
                        textAlign: "center",
                      }}
                    >
                      <ErrorMessage name="matKhau" />
                    </div>
                  ) : (
                    <div
                      className="text-danger"
                      style={{
                        height: "20px",
                        fontSize: "14px",
                        textAlign: "center",
                      }}
                    >
                      <p>Mt khu khng ng!</p>
                    </div>
                  )}
                </div>
                <div style={{ textAlign: "right", marginTop: "20px" }}>
                  <button
                    type="button"
                    style={{ marginRight: "5px" }}
                    className="btn btn-secondary"
                    onClick={() => {
                      setChangePassword(true);
                    }}
                  >
                    i mt khu
                  </button>
                  <button type="submit" className="btn btn-secondary">
                    Cp nht
                  </button>
                </div>
              </div>
            </div>
          </form>
        )}
      </Formik>
    );
  };

  const changePasswordValidate = Yup.object().shape({
    currentPassword: Yup.string()
      .required("Mt khu khng c b trng")
      .min(6, "Mt khu cn di hn 6 k t"),
    newPassword: Yup.string()
      .required("Mt khu khng c b trng")
      .min(6, "Mt khu cn di hn 6 k t"),
    newPasswordAgain: Yup.string()
      .required("Mt khu khng c b trng")
      .min(6, "Mt khu cn di hn 6 k t"),
  });

  const renderHistoryBooking = () => {
    console.log(userInfo);
    return userInfo.thongTinDatVe.map((item, index) => {
      return (
        <div
          key={index}
          className="d-flex align-items-center history-booking__items"
        >
          <img src="/img/loading.png" alt="/img/loading.png" />
          <div>
            <p>
              Phim: <span style={{ fontWeight: "bold" }}> {item.tenPhim} </span>
            </p>
            <p>
              Thi lng:
              <span style={{ fontWeight: "bold" }}>
                {" "}
                {item.thoiLuongPhim} pht
              </span>{" "}
            </p>
            <p>
              Gi: <span style={{ fontWeight: "bold" }}> {item.giaVe} </span>
            </p>
            <p>{item.danhSachGhe[0].tenHeThongRap}</p>
            <p>{item.danhSachGhe[0].tenCumRap}</p>
            <p>
              {" "}
              Gh:
              {item.danhSachGhe.map((chair, indexChair) => {
                return (
                  <span key={indexChair} style={{ fontWeight: "bold" }}> {chair.tenGhe}</span>
                );
              })}
            </p>
            <p>
              Ngy t v:
              <span style={{ fontWeight: "bold" }}>
                {" "}
                {item.ngayDat.substring(0, 10)}
              </span>{" "}
            </p>
          </div>
        </div>
      );
    });
  };

  return (
    <>
      {userInfo === null ? (
        <Loading />
      ) : (
        <div className="customer">
          <div className="customer__content">
            <Tabs defaultActiveKey="info">
              <Tab eventKey="info" title="Thng tin c nhn">
                {renderInfo()}
              </Tab>
              <Tab eventKey="historybooking" title="Lch s t v">
                <div className="history-booking">{renderHistoryBooking()}</div>
              </Tab>
            </Tabs>
          </div>
        </div>
      )}
      <Modal
        size="sm"
        id="modal-timeup"
        show={modalShow}
        aria-labelledby="contained-modal-title-vcenter"
        centered
        onHide={handleClose}
      >
        <Modal.Body>
          <p className="text-danger">Cp nht thnh cng</p>
          <button
            onClick={() => {
              handleClose();
            }}
            className="btn btn-secondary mt-3"
          >
            ng
          </button>
        </Modal.Body>
      </Modal>
      <Modal
        show={changePassword}
        id="modal-changePassword"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        onHide={handleClose}
      >
        <Modal.Body>
          <Formik
            initialValues={{
              currentPassword: "",
              newPassword: "",
              newPasswordAgain: "",
            }}
            onSubmit={(values) => {
              if (values.currentPassword !== userInfo.matKhau) {
                setIsPasswordChange(false);
              }
              if (values.currentPassword === userInfo.matKhau) {
                if (values.newPasswordAgain !== values.newPassword) {
                  setCheckNewPassword(false);
                } else {
                  setCheckNewPassword(true);

                  const userDispatch = {
                    taiKhoan: user?.taiKhoan,
                    matKhau: values.newPassword,
                    email: user?.email,
                    soDt: user?.soDT,
                    maNhom: user?.maNhom,
                    maLoaiNguoiDung: user?.maLoaiNguoiDung,
                    hoTen: user?.hoTen,
                  };
                  dispatch(updateAction(userDispatch));
                  setChangePasswordDone(true);
                }
              }
            }}
            validationSchema={changePasswordValidate}
            render={(formikProps) => (
              <Form>
                <div className="form-group m-0">
                  <Field
                    type="password"
                    name="currentPassword"
                    className="form-control"
                    placeholder="Mt khu hin ti"
                    onChange={formikProps.handleChange}
                    onClick={() => setIsPasswordChange(true)}
                  />
                  {isPasswordChange ? (
                    <div
                      className="text-danger text-center"
                      style={{ height: "20px", margin: " 5px 0" }}
                    >
                      <ErrorMessage name="currentPassword" />
                    </div>
                  ) : (
                    <div
                      className="text-danger text-center"
                      style={{ height: "20px", margin: " 5px 0" }}
                    >
                      <p>Mt khu hin ti khng ng</p>
                    </div>
                  )}
                </div>
                <div className="form-group" style={{ marginBottom: "10px" }}>
                  <Field
                    type="password"
                    name="newPassword"
                    className="form-control"
                    placeholder="Mt khu mi"
                    onChange={formikProps.handleChange}
                  />
                  <div
                    className="text-danger text-center"
                    style={{ height: "20px", margin: " 5px 0" }}
                  >
                    <ErrorMessage name="newPassword" />
                  </div>
                </div>
                <div className="form-group m-0">
                  <Field
                    type="password"
                    name="newPasswordAgain"
                    className="form-control"
                    placeholder="Nhp li mt khu mi"
                    onChange={formikProps.handleChange}
                    onClick={() => setCheckNewPassword(true)}
                  />
                  {checkNewPassword ? (
                    <div
                      className="text-danger text-center"
                      style={{ height: "20px", margin: " 5px 0" }}
                    >
                      <ErrorMessage name="newPasswordAgain" />
                    </div>
                  ) : (
                    <div
                      className="text-danger text-center"
                      style={{ height: "20px", margin: " 5px 0" }}
                    >
                      <p>Mt khu mi v mt khu nhp li khng trng khp</p>
                    </div>
                  )}
                </div>
                {changePasswordDone ? (
                  <div
                    className="text-success text-center"
                    style={{ height: "20px" }}
                  >
                    <p>i mt khu thnh cng</p>
                  </div>
                ) : (
                  <div
                    className="text-success text-center"
                    style={{ height: "20px" }}
                  ></div>
                )}
                <div className="text-center">
                  <button type="submit" className="btn btn-secondary mt-4">
                    i mt khu
                  </button>
                </div>
              </Form>
            )}
          />
          <a
            onClick={() => {
              handleClose();
            }}
          >
            <i className="fa fa-times"></i>
          </a>
        </Modal.Body>
      </Modal>
    </>
  );
}
